.navbar{

    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1.5rem;
   .navbar_btn{
    display: none;
   }
    .logo{
        h2{
            font-size: 2.4rem;
            font-weight: bold;
            font-family: 'Lobster', cursive;
             @media screen and (max-width:1000px) {
                font-size: 1.5rem;
             }
        }
    }
    .nav_menu{
        display: flex;
        list-style: none;
        justify-content: space-around;
        width: 50%;
        
        li{
            margin: 0 2rem;
            font-size: 1.6rem;
            @media screen and (max-width:1000px) {
                font-size: 1rem;
             }
            &:hover{
                text-shadow: 4px 2px 2px gray;
            }
        }
    }
    .toggle_menu{
        display: none;
    }
    .nav_end{
        display: flex;
        li{
            padding: 0 1rem;
            font-size: 1rem;
        }
    }
    
}
 @media screen and (max-width:800px) {
        .navbar{
            justify-content: space-between;
            .mobil_menu{
                display: flex;
                flex-direction: column;
                
                .toggle_menu{
                    display: flex;
                    flex-direction: column;
                    position: absolute;
                    background: #f6efe1;
                    width: 100%;
                    left: 0;
                    top: 60px;
                    
                    z-index: 9999;
                    li{
                        text-align: center;
                        
                        font-size: 1rem;
                        padding: 1.4rem 0;
                        a{
                            &:hover{
                                text-decoration: underline;
                            }
                        }
                    }
                    .toggle_icon{
                        display: flex;
                        justify-content: center;
                        li{
                            padding: 0 1rem;
                        }
                        
                    }
            }
            }
           
            .nav_menu{
                display: none;
            }
            .nav_end{
                display: none;
            }
            .navbar_btn{
                display: flex;
                position: absolute;
                right: 10px;
                font-size: 1.5rem;
                cursor: pointer;
                
            }
        }
    }