.section1{
  .slide{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .slide_box{
      width: 100%;
      padding-top: 50px;
      .swiper-slide {
        background-position: center;
        background-size: cover;
        width: 500px;
        height: 500px;
        @media screen and (max-width:800px) {
        width: 300px;
        height: 300px;
        }
        @media screen and (max-width:500px) {
        width: 200px;
        height: 200px;
        }
        img{
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 5px;
        }
      }
    }
  }
  p{
    text-align: center;
    font-size: 2rem;
    padding-top: 3rem;
     @media screen and (max-width:800px) {
        font-size: .8rem;
        line-height: 150%;
    }
  }
}
.autoplay-progress {
  position: absolute;
  right: 100px;
  bottom: 16px;
  z-index: 10;
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: #000;
  
}
.autoplay-progress svg {
  --progress: 0;
  position: absolute;
  right: 0;
  top: 0px;
  z-index: 10;
  width: 100%;
  height: 100%;
  stroke-width: 4px;
  stroke: var(--swiper-theme-color);
  fill: none;
  stroke-dashoffset: calc(125.6 * (1 - var(--progress)));
  stroke-dasharray: 125.6;
  transform: rotate(-90deg);
}