.section2{
    width: 100%;
    height: auto;
    .title{
        font-size: 3rem;
        padding: 5rem 0;
        text-align: center;
        @media screen and (max-width:800px){
            padding: 1.8rem 0;
        }
    }
  
    .simple_recipe{
        display: flex;
        height: 500px;
        @media screen and (max-width:800px) {
            flex-direction: column;
            height: auto;
        }
        .simple_left{
            width: 40%;
            height: 100%;
            padding-right: 1rem;
            @media screen and (max-width:800px) {
            width: 100%;
            }
            .mySwiper2 {
                
                .swiper-slide{
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }
        .simple_right{
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: relative;
            @media screen and (max-width:800px) {
            width: 100%;
            flex-direction: column-reverse;
            }
            .simple_info{
                flex: 60%;
                
                 @media screen and (max-width:800px) {
                    text-align: center;
                  
                 }
                p{
                   &:nth-child(1){
                        font-size: 2rem;
                        padding-bottom: 4rem;
                        @media screen and (max-width:800px) {
                            font-size: 1.2rem;
                            padding-top: 1rem;
                            padding-bottom: 1rem;
                        }
                    }
                    &:nth-child(2){
                        font-size: 1rem;
                        @media screen and (max-width:800px) {
                            font-size: .7rem;
                            
                        }
                    }
                }
            }
            .simple_slide{
                flex: 30%;
                width: 100%;
                height: 100%;
                
                .mySwiper {
                    height: 100%;
                    box-sizing: border-box;
                    padding: 10px 0;
                    .swiper-slide {
                        width: 100%;
                        height: 100%;
                        opacity: 0.4;
                        .simple_slide_box{
                            cursor: pointer;
                            padding: .2rem .2rem;
                            border-radius: 10px;
                            
                            &:hover p {
                                text-decoration: underline;
                            }
                            img {
                                width: 100%;
                                height: 100px;
                                object-fit: cover;
                            }
                            p{
                                text-align: center;
                                padding: .5rem 0;
                            }
                        }
                        
                    }
  
                    .swiper-slide-thumb-active {
                        opacity: 1;
                        background: #282828;
                        color: #fff;
                    }
                }
            }    
        }
    }
}

  